


































.validateBox{height: auto;position: relative;border-bottom: 1px solid #ddd;padding: 10px 0;margin-left: 60px;}
.validateBox .operate{width: 80px;min-height:100px;position: absolute;text-align: center;left: 0;top: 10px;}
.validateBox .operate .operate-sql{width: 80px;height:25px;line-height:25px;position: absolute;right: 0;top: 0;background-color: #66cc00;border-radius: 5px;}
.validateBox .operate .edit{width: 30px;height:30px;position: absolute;left: 0;top: 30px;background: url(img/icon_edit.png) no-repeat center;background-size: 60%;cursor: pointer;}
.validateBox .operate .del{width: 30px;height:30px;position: absolute;right: 0;top: 30px;background: url(img/icon_del.png) no-repeat center;background-size: 90%;cursor: pointer;}
.validateBox .operate .textareabox{margin-left: 110px;overflow: hidden;}
.validateBox .dt textarea{width: 49%;}
.validateBox .dt textarea:first-child{float: left;}
.validateBox .dt textarea:last-child{float: right;}



