.title{height: 30px;line-height: 30px;font-size: 24px;}
.testcasePage .api_name{line-height: 18px;overflow: hidden;color: #aaaaaa;padding: 10px 0;border-bottom: 1px solid #ddd;}
.testcasePage .api_name i{float: left;width:18px;height: 18px;background: url(img/icon_setting.png) no-repeat center;background-size: 100%;margin-right: 10px;}
.testcasePage .api_name span{float: left;}
.module{width: 100%;height: auto;}
.module > .title{width: 100%;height: 50px;line-height: 50px;border-bottom: 1px solid #ddd;font-size: 18px;color: #666;}
.module ul{width: 100%;height: auto;}
.module ul li{width: 100%;height: auto;position: relative;padding: 10px 0;}
.module ul li label{width: 150px;text-align: right;height: 34px;line-height: 34px;position: absolute;left: 0;top: 10px;font-weight: 400;font-size: 16px;color: #000;}
.module ul li .tag{margin-left: 170px;}
.module ul li .tag > *{display: block;width: 100%;}
.module ul li .tag input{height: 34px;line-height: 34px;}
.module ul li .tag span{height: 30px;line-height: 30px;}
.addBtn{margin-left:60px;padding: 20px 10px;overflow: hidden;display: inline-block;cursor: pointer;}
.addBtn i{display: block;width: 30px;height: 30px;background: url(img/icon_add.png) no-repeat center;background-size: 80%;margin-right: 10px;float: left;}
.addBtn span{display: inline-block;height: 30px;line-height:30px;float: left;font-weight: 400;font-size: 18px;}
.save-cancel{padding-right:20px;padding-top:20px;width: 100%;height:80px;position: relative;background-color: #f5f5f5;}
.save-cancel > .btn{width: 120px;height: 40px;line-height: 40px;text-align: center;position: absolute;top:50%;margin-top: -20px;border-radius: 5px;font-size: 18px;cursor: pointer;}
.save-cancel > .save{left: 30%;margin-left: -60px;background-color: #5cb85c;color: #fff}
.save-cancel > .cancel{right: 30%;margin-right: -60px;border:1px solid #ddd;background-color: #fff;}

.validateBox{height: auto;position: relative;border-bottom: 1px solid #ddd;padding: 10px 0;margin-left: 60px;}
.validateBox .operate{width: 80px;min-height:100px;position: absolute;text-align: center;left: 0;top: 10px;}
.validateBox .operate .operate-json{width: 80px;height:25px;line-height:25px;position: absolute;right: 0;top: 0;background-color: #ffcc00;border-radius: 5px;}
.validateBox .operate .edit{width: 30px;height:30px;position: absolute;left: 0;top: 30px;background: url(img/icon_edit.png) no-repeat center;background-size: 60%;cursor: pointer;}
.validateBox .operate .del{width: 30px;height:30px;position: absolute;right: 0;top: 30px;background: url(img/icon_del.png) no-repeat center;background-size: 90%;cursor: pointer;}
.validateBox .textareabox{margin-left: 110px;overflow: hidden;}
.validateBox .ot textarea{width: 100%;}
.validateBox .iconbtn{  
  position: absolute;
  right: 0;
  top: 30px;
  cursor: pointer;}

  


.validate_error{border:1px solid red !important;}



