  font-family: Helvetica Neue, Arial, sans-serif;
  font-size: 14px;
  color: #444;
}

.api_grid{
  border: 2px solid #fff;
  border-radius: 3px;
  background-color: #fff;
}

.btn>span: {
  color: #444;
  vertical-align:middle!important;
}

.btn svg: {
  color: #fff;
}

.api_grid_th{
  background-color: Gray;
  color: rgba(255,255,255,0.66);
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -user-select: none;
}

.api_grid_input { 
  border: 1px solid #eee;
  border-radius: 0px; 
  width:100%;
}
.api_grid_td {
  border:1px solid #FFF;
  width:20%;
}
.api_grid_td_action {
  background-color: #E6E6E6
}


.api_grid_th, .api_grid_td {
  min-width: 10px;
  padding: 5px 5px;
}

.api_grid_th.active {
  color: #fff;
}

.api_grid_th.active .arrow {
  opacity: 1;
}

.grid_bottom_btn_row {
  padding-left:15px;
  padding-top:5px
}
.arrow {
  display: inline-block;
  vertical-align: middle;
  width: 0;
  height: 0;
  margin-left: 5px;
  opacity: 0.66;
}

.arrow.asc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-bottom: 4px solid #fff;
}
.td_font_red {
  color: red;
}

.td_font_blue {
  color: blue
}
.arrow.dsc {
  border-left: 4px solid transparent;
  border-right: 4px solid transparent;
  border-top: 4px solid #fff;
}
.param-level-0 {
    background-color: #b8def5;
}
.param-level-1 {
    background-color: #eddff3;
}
.param-level-2 {
    background-color: #d0ebf2;
}
.param-level-3 {
    background-color: #e8f1df;
}
.param-level-4 {
    background-color: #f1e0df;
}
.param-level-5 {
    background-color: #f1ebdf;
}
.param-level-6 {
    background-color: #e5e4f2;
}
.param-level-7 {
    background-color: #b8def5;
}
.param-level-8 {
    background-color: #eddff3;
}
.param-level-9 {
    background-color: #d0ebf2;
}
.param-level-10 {
    background-color: #e8f1df;
}
#search {
  margin-bottom: 10px;
}



